// src/components/spatial/NodeSpace.tsx
"use client";
import { motion, useMotionValue, useSpring } from "framer-motion";
import React, { useEffect, useState } from "react";

export function NodeSpace({ onIntentChange }: { onIntentChange: (val: number) => void }) {
  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);
  const [focusLevel, setFocusLevel] = useState(0);

  useEffect(() => {
    const handleMove = (e: MouseEvent) => {
      mouseX.set(e.clientX);
      mouseY.set(e.clientY);
      
      // If they stop moving, 'Intent' increases. 
      // High speed = browsing (boring). Low speed/stills = considering (interesting).
    };
    window.addEventListener("mousemove", handleMove);
    return () => window.removeEventListener("mousemove", handleMove);
  }, []);

  return (
    <div className="relative h-full w-full">
      {/* Floating Nodes: Not a grid. A cluster. */}
      <ArtifactNode x="20%" y="30%" label="Reduction" content="We killed the nav. Conversion went up." />
      <ArtifactNode x="65%" y="15%" label="Friction" content="A 2-second delay made the user trust the result." />
      <ArtifactNode x="45%" y="70%" label="Non-Standard" content="Why use a button when you can use a gesture?" />
      
      {/* Invisible Trigger Zones that boost 'Gravity' */}
      <div 
        className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-64 h-64 rounded-full border border-white/5"
        onMouseEnter={() => onIntentChange(90)}
        onMouseLeave={() => onIntentChange(0)}
      />
    </div>
  );
}

function ArtifactNode({ x, y, label, content }: any) {
  const [open, setOpen] = useState(false);
  return (
    <motion.div 
      style={{ left: x, top: y }}
      className="absolute"
      whileHover={{ scale: 1.1 }}
      onClick={() => setOpen(!open)}
    >
      <div className="h-4 w-4 bg-white/20 rotate-45 border border-white/50 cursor-pointer" />
      <div className="mt-4 font-mono text-[9px] uppercase tracking-tighter opacity-40">{label}</div>
      {open && (
        <motion.div initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} className="absolute top-12 left-0 w-48 text-xs leading-relaxed text-zinc-400 bg-black/80 p-4 border border-white/10 backdrop-blur-md">
          {content}
        </motion.div>
      )}
    </motion.div>
  );
}