// src/app/page.tsx
"use client";
import React, { useState, useEffect } from "react";
import { NodeSpace } from "@/components/spatial/NodeSpace";
import { SystemHUD } from "@/components/spatial/SystemHUD";
import { ContactOverlay } from "@/components/spatial/ContactOverlay";

export default function Page() {
  const [gravity, setGravity] = useState(0); // 0 to 100 based on 'intent'
  
  return (
    <main className="h-screen w-screen bg-[#020202] overflow-hidden text-zinc-300 select-none">
      {/* The background is a living coordinate grid */}
      <div className="absolute inset-0 opacity-20 pointer-events-none" 
           style={{ backgroundImage: 'radial-gradient(#ffffff1a 1px, transparent 1px)', backgroundSize: '40px 40px' }} />
      
      <NodeSpace onIntentChange={setGravity} />
      
      <SystemHUD intent={gravity} />
      
      {/* Only appears when they've 'earned' it through behavior */}
      {gravity > 80 && <ContactOverlay />}
    </main>
  );
}